// Mobile
.social {
  width: 132px;
  height: 22px;
  margin: 0 auto;
  margin-bottom: 40px;
}

.social__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  position: relative;
}

.social__list::after {
  content: "";
  position: absolute;
  background-color: #d9d9d9;
  width: 280px;
  height: 1px;
  left: -73px;
  top: 45px;
}
